        amount: 0.010000001,
        network: 'BTC',
        status: 'success',
      },
    ],
  }),
  computed: {
    ...mapGetters({
      currency: 'popups/exchangeSelectedCurrency',
      wallets: 'auth/wallets',
      deposits: 'auth/deposits',
    }),
  },
  methods: {
    deposit() {
      let amount = +this.amount

      if (isNaN(amount)) {
        this.handleError({
          message: 'Amount must be a number!',
        })
      } else {
        this.loading = true
        this.$axios
          .post('/api/wallet/deposit/create', {
            amount,
            currency: this.currency,
          })
          .then(response => {
            this.handleSuccess(response.data)
            this.loading = false
          })
          .catch(err => {
            this.loading = false
            this.handleError(err.response.data)
            console.log(err.response)
          })
      }
    },
    handleError(response) {
      if (response) {
        // this.$store.commit('auth/ADD_TRANSACTION', response.deposit)
        this.$store.commit('popups/ADD_ALERT', {
          type: 'error',
          title: 'Deposit request failed',
          message: response.length < 200 ? response.message : 'Message is too long',
        })
      }
    },
    handleSuccess(response) {
      this.$store.commit('auth/ADD_TRANSACTION', response.deposit)
      this.$store.commit('popups/ADD_ALERT', {
        type: 'message',
        title: 'Deposit requested!',
        message: response.message,
      })
    },
  },
  mounted() {
    this.$anime({
      targets: '.deposit',
      scale: [0.95, 1],
      easing: 'easeOutExpo',
      duration: 800,
    })
  },
}
